import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Can } from "@/components/auth/can";
import { DeleteButton } from "@/components/delete-button";
import { Badge } from "@/components/ui/badge";
import { Shield, User, Activity, Clock, AlertTriangle } from "lucide-react";
import { createClient } from "@/utils/supabase/server"; // <--- Using Real DB Client
import { redirect } from "next/navigation";

export default async function Dashboard() {
  const supabase = await createClient();

  // 1. GET REAL USER
  const { data: { user }, error } = await supabase.auth.getUser();
  if (error || !user) redirect("/login");

  // 2. GET REAL ROLE
  const { data: userRole } = await supabase
    .from('user_roles')
    .select('roles ( name )')
    .eq('user_id', user.id)
    .single();

  const roleName = userRole?.roles ? (userRole.roles as any).name : 'No Role';

  const getRoleColor = (role: string) => {
    if (role === 'Admin') return "bg-blue-600";
    if (role === 'Sales Manager') return "bg-emerald-600";
    if (role === 'User') return "bg-indigo-500";
    return "bg-slate-500";
  };

  return (
    <div className="max-w-7xl mx-auto p-8 space-y-8">
      {/* Header */}
      <div className="flex flex-col gap-1">
        <h1 className="text-3xl font-bold tracking-tight text-slate-900">Dashboard Overview</h1>
        <p className="text-slate-500">Welcome back, {user.email}</p>
      </div>

      {/* Stats Grid */}
      <div className="grid gap-4 md:grid-cols-3">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium text-slate-600">User Identity</CardTitle>
            <User className="h-4 w-4 text-slate-400" />
          </CardHeader>
          <CardContent>
            <div className="text-xl font-bold truncate">{user.email?.split('@')[0]}</div>
            <p className="text-xs text-muted-foreground font-mono mt-1">ID: {user.id.slice(0, 8)}...</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium text-slate-600">Access Level</CardTitle>
            <Shield className="h-4 w-4 text-slate-400" />
          </CardHeader>
          <CardContent>
            <Badge className={`${getRoleColor(roleName)} hover:${getRoleColor(roleName)} text-sm px-3 py-1`}>
              {roleName}
            </Badge>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium text-slate-600">System Status</CardTitle>
            <Activity className="h-4 w-4 text-green-500" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">Online</div>
            <p className="text-xs text-muted-foreground mt-1">Connected to Supabase</p>
          </CardContent>
        </Card>
      </div>

      <div className="grid gap-4 md:grid-cols-7">
        {/* Activity Feed */}
        <Card className="col-span-4 h-full">
          <CardHeader>
            <CardTitle className="flex items-center gap-2"><Clock className="w-5 h-5" /> Activity</CardTitle>
            <CardDescription>Real-time session activity.</CardDescription>
          </CardHeader>
          <CardContent>
             <div className="space-y-6">
              <div className="flex items-center gap-4">
                <div className="w-2 h-2 rounded-full bg-blue-500" />
                <p className="text-sm">User <strong>{user.email}</strong> authenticated securely.</p>
              </div>
             </div>
          </CardContent>
        </Card>

        {/* Restricted Zone */}
        <Card className="col-span-3 border-red-100 bg-red-50/50">
          <CardHeader>
            <div className="flex items-center gap-2 text-red-600">
              <AlertTriangle className="h-5 w-5" />
              <CardTitle>Restricted Zone</CardTitle>
            </div>
            <CardDescription>Requires <strong>users.delete</strong> permission.</CardDescription>
          </CardHeader>
          <CardContent>
             <Can 
              perform="users.delete" 
              fallback={
                <div className="flex flex-col items-center justify-center p-8 bg-white/60 rounded-lg border border-dashed border-red-200 text-center">
                   <Shield className="h-10 w-10 text-red-200 mb-3" />
                   <p className="text-sm font-bold text-red-600">Access Denied</p>
                   <p className="text-xs text-muted-foreground mt-1">
                     Role: {roleName}
                   </p>
                </div>
              }
            >
              <div className="p-6 bg-white rounded-lg border shadow-sm">
                <p className="text-sm font-medium mb-4">Database Controls</p>
                <DeleteButton />
              </div>
            </Can>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}